body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-hind-siliguri: "Hind Siliguri";
  --font-open-sans: "Open Sans";

  /* font sizes */
  --font-size-base: 16px;
  --font-size-11xl: 30px;
  --font-size-mid: 17px;
  --font-size-lg: 18px;
  --font-size-17xl: 36px;
  --font-size-5xl: 24px;
  --font-size-sm: 14px;

  /* Colors */
  --color-white: #fff;
  --color-gray: #232323;
  --color-darkslategray: #303030;
  --color-whitesmoke-100: #f3f5f6;
  --color-dimgray-100: #686868;
  --color-brown-100: #9e3040;

  /* border radiuses */
  --br-8xs: 5px;
  --br-3xs: 10px;
  --br-12xs-2: 0.2px;
  --br-11xl: 30px;
  --br-mini: 15px;
}
